<script>
import { Decimal } from 'decimal.js'
import { mapGetters } from 'vuex'
import HistoryOrder from 'models/HistoryOrder'
import User from 'models/User'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'

export default {
  name: 'HistoryMarketplaceOrderBreakdownSummary',
  props: {
    details: {
      type: Object,
      default: () => ({
        deliveryFee: 0,
        minBasketSurcharge: 0,
        products: {
          total: 0
        },
        discountsTotal: 0,
        customerCashPaymentAmount: null,
        paymentMethod: ''
      })
    }
  },
  computed: {
    ...mapGetters('authentication', [GET_CURRENT_STORE_ADMIN_GETTER]),
    user() {
      return User.parse(this[GET_CURRENT_STORE_ADMIN_GETTER] || {})
    },
    totalPrice() {
      if (!this.details || !this.details.products) {
        return 0
      }
      return new Decimal(this.details.deliveryFee || 0)
        .plus(this.details.products.total || 0)
        .minus(this.details.discountsTotal || 0)
    },
    totalProductsPrice() {
      if (!this.details || !this.details.products) {
        return 0
      }
      return this.details.products.total || 0
    },
    minBasketSurcharge() {
      return (this.details && this.details.minBasketSurcharge) || 0
    },
    deliveryFee() {
      return new Decimal((this.details && this.details.deliveryFee) || 0).minus(
        this.minBasketSurcharge
      )
    },
    discountsTotal() {
      return (this.details && this.details.discountsTotal) || 0
    },
    discountsExist() {
      return this.discountsTotal > 0
    },
    isCustomerCashAmount() {
      return (this.details && this.details.customerCashPaymentAmount) || null
    },
    isCashPayment() {
      return this.details && this.details.paymentMethod === 'CASH'
    },
    shouldSeeOrderBreakdown() {
      if (HistoryOrder.isFinished(this.details)) {
        return false
      }
      if (!HistoryOrder.isAccepted(this.details)) {
        return false
      }
      if (this.user.isMCD()) {
        return false
      }
      return this.user.isAllowedToManageOrders()
    },
    showEstimatePriceHint() {
      if (!this.details) {
        return false
      }
      return !this.shouldSeeOrderBreakdown && !this.user.isMCD()
    },
    formattedAmount() {
      return this.formatCurrency(this.details.customerCashPaymentAmount)
    }
  },
  methods: {
    formatCurrency(value) {
      return this.$options.filters['currency'](value)
    }
  }
}
</script>

<template>
  <div class="order-breakdown-summary">
    <div class="summary-title">
      {{ $t('order.summary.breakdown') }}
    </div>
    <div class="summary-detail highlighted strong">
      <label>{{ $t('order.summary.productPrice') }}</label>
      <strong>
        {{ totalProductsPrice | currency }}
      </strong>
    </div>
    <div class="summary-detail">
      <label>{{ $t('order.summary.minBasketSurcharge') }}</label>
      <strong>{{ minBasketSurcharge | currency }}</strong>
    </div>
    <div class="summary-detail">
      <label>{{ $t('order.summary.deliveryFee') }}</label>
      <strong>{{ deliveryFee | currency }}</strong>
    </div>
    <div class="summary-detail summary-detail-discounts">
      <div class="summary-detail-discounts-amount">
        <label>{{ $t('order.summary.discounts') }}</label>
        <strong
          ><span v-if="discountsExist" class="summary-detail-discounts-minus"
            >-</span
          >{{ discountsTotal | currency }}</strong
        >
      </div>
    </div>
    <div class="separator"></div>
    <div
      data-qa="order-details-total"
      class="summary-detail strong summary-detail-total"
    >
      <label>{{ $t('order.summary.totalPrice') }}</label>
      <strong>
        <img
          v-if="isCashPayment"
          data-qa="order-details-cash-icon"
          src="~assets/images/banknote.svg"
          :alt="$t('order.messages.customer.payment.CASH')"
        />
        {{ totalPrice | currency }}</strong
      >
    </div>
    <span v-if="isCustomerCashAmount" class="summary-detail-customer-text">
      ({{
        $t('order.summary.customer.cash', {
          amount: formattedAmount
        })
      }})
    </span>
    <span v-if="showEstimatePriceHint" class="order-breakdown-summary-hint">{{
      $t('order.summary.hint')
    }}</span>
  </div>
</template>
